<template lang="pug">
#map.absolute.absolute--fill
</template>

<script>
import mapboxgl from 'mapbox-gl/dist/mapbox-gl-dev.js';
import U from 'mapbox-gl-utils';
import { EventBus } from '../EventBus';
import axios from 'axios';
import MapboxGeocoder from '@mapbox/mapbox-gl-geocoder';
import '@mapbox/mapbox-gl-geocoder/dist/mapbox-gl-geocoder.css';
const turf = require('@turf/turf');
// import { addCsvByUrl, addCsvByRows } from './csv-geo-au';
let boundary;
export default {
    async mounted() {
        // replace this Mapbox access token with your own
        // mapboxgl.accessToken = 'pk.eyJ1Ijoic3RldmFnZSIsImEiOiJGcW03aExzIn0.QUkUmTGIO3gGt83HiRIjQw';
        mapboxgl.accessToken =
            'pk.eyJ1Ijoic2JjaGVycmUiLCJhIjoiY2s2cTkxcHVxMXVnNzNsbDl2bTN1Mm83dyJ9.sU2NJpziux-m0MCCkm41iQ';
        const map = new mapboxgl.Map({
            container: 'map',
            center: [144.96, -37.8],

            zoom: 10,
            style: 'mapbox://styles/mapbox/outdoors-v11',
            // style: { version: 8, sources: {}, layers: []},
            hash: true,
        });
        const geocoder = new MapboxGeocoder({
            accessToken: mapboxgl.accessToken,
            mapboxgl: mapboxgl,
            zoom: 12,
        });
        map.addControl(geocoder, 'top-left');
        U.init(map, mapboxgl);
        window.map = map;
        window.app.Map = this;
        boundary = (await axios.get('out-concave.geojson')).data;

        map.U.onLoad(() => {
            map.U.addGeoJSON('boundary', boundary);
            map.U.addLine('boundary-line', 'boundary', {
                lineColor: 'red',
                lineDasharray: [4, 4],
            });
            map.U.addGeoJSON('circle');
            map.U.addLine('circle-line', 'circle', {
                lineColor: 'green',
                lineDasharray: [4, 4],
            });
            map.U.addGeoJSON('centre');
            map.U.addCircle('centre-circle', 'centre', {
                circleColor: 'blue',
            });
            map.U.addGeoJSON('intersection');
            map.U.addFill('intersection-fill', 'intersection', {
                fillColor: 'hsla(150,80%,50%,0.3)',
            });
            map.U.addGeoJSON('roads');
            map.U.addLine('roads-line', 'roads', {
                lineColor: 'black',
                lineWidth: 2,
            });
            map.U.addGeoJSON('roads-clipped', turf.featureCollection([]), {
                generateId: true,
            });
            // map.addSource('roads-clipped', {
            //     type: 'geojson',
            //     data: turf.featureCollection([]),
            //     generateId: true,
            // });
            map.U.addLine('roads-clipped', 'roads-clipped', {
                lineColor: [
                    'case',
                    ['to-boolean', ['feature-state', 'hover']],
                    'cyan',
                    'blue',
                ],
                lineWidth: 3,
            });
            map.on('click', ({ lngLat }) => {
                updateLocation(map, [lngLat.lng, lngLat.lat]);
            });
            geocoder.on('result', ({ result }) => {
                console.log(result);
                updateLocation(map, result.geometry.coordinates);
            });
            map.U.hoverPopup(
                'roads-clipped',
                f => `${JSON.stringify(f.properties)}`,
            );
            map.U.hoverFeatureState('roads-clipped');
        });
    },
};

function updateLocation(map, centre) {
    // const centre = [map.getCenter().lng, map.getCenter().lat];
    // const centre = [lngLat.lng, lngLat.lat];
    map.U.setData('centre', turf.point(centre));
    const stats = {
        within: turf.booleanWithin(turf.point(centre), boundary),
    };
    console.log(stats.within);
    if (stats.within) {
        const circle = turf.circle(centre, 5);
        map.U.setData('circle', circle);
        const intersection = turf.intersect(circle, boundary);
        map.U.setData('intersection', intersection);
        stats.area = turf.area(intersection);
        EventBus.$emit('homestats', stats);

        // const parks = map.querySourceFeatures('composite', {
        //     sourceLayer: 'landuse',

        const doRoads = false;
        if (doRoads) {
            const roads = map.querySourceFeatures('composite', {
                sourceLayer: 'road',
                filter: ['==', ['geometry-type'], 'LineString'],
            });
            const roadsWithin = roads
                .filter(r => !turf.booleanDisjoint(r, intersection))
                .map(turf.flatten)
                .flatMap(fc => fc.features);
            console.log({ roadsWithin });
            map.U.setData('roads', turf.featureCollection(roadsWithin));
            // const roadsClipped = roadsWithin.map(r =>
            //     turf.lineSplit(r, intersection),
            // );
            const polygonify = line => ({
                ...line,
                geometry: {
                    type: 'Polygon',
                    coordinates: [
                        [
                            ...line.geometry.coordinates,
                            ...line.geometry.coordinates.reverse(),
                        ],
                    ],
                },
            });
            const roadsClipped = roadsWithin
                .map(r => turf.intersect(polygonify(r), intersection))
                .filter(Boolean);

            const roadsClippedFC = turf.featureCollection(
                roadsClipped,
                // roadsClipped.flatMap(fc =>
                //     fc.features.filter(r =>
                //         turf.booleanWithin(r, intersection),
                //     ),
                // ),
            );
            console.log({ roadsClippedFC });
            map.U.setData('roads-clipped', roadsClippedFC);
        }
    } else {
        map.U.setData('circle', turf.featureCollection([]));
        map.U.setData('intersection', turf.featureCollection([]));
        EventBus.$emit('homestats', stats);
    }
}
import 'mapbox-gl/dist/mapbox-gl.css';
</script>

<style scoped>
</style>
